/**
 * list 
 *
 * @prop auto - 
 * @prop autoHideScroller - 
 * @prop autoHidePage - 
 * @prop item - 
 * @prop page - item
 * @prop pager - 
 * @prop pageSize - item
 * @prop pageType - more | num
 * @prop pageTrigger - scroll | click
 * @prop height - 
 *
 * @event switchPage - 
 * @event scrollerChange - /
 *
 * @slot loadMore - 
 *
 * @slotScope - 
 */

import '../../scss/common/main.scss'
import './List.scss'
import render from './List.render'

import Icon from '../Icon/Icon'
import Loading from '../Loading/Loading'
import Pager from '../Page/Page'
import Scroller from '../Scroller/Scroller'

import baseMixin from '../../mixin/base'
import apiMixin from './List.api'
import listMixin from '../../mixin/list'

const PAGE_TYPE_NUM = 'num'
const PAGE_TYPE_MORE = 'more'

export default {
  name: 'List',

  render,

  mixins: [baseMixin, listMixin, apiMixin],

  components: {
    icon: Icon,
    loading: Loading,
    page: Pager,
    scroller: Scroller
  },

  props: {
    auto: {
      type: Boolean,
      default: false
    },
    item: {
      type: Array,
      default: () => []
    },
    page: Object,
    pager: {
      type: Boolean,
      default: false
    },
    pageSize: {
      type: Number,
      default: 5
    },
    pageType: {
      type: String,
      default: 'num',
      validator(val) {
        return ['num', 'more'].includes(val)
      }
    },
    pageTrigger: {
      type: String,
      default: 'scroll',
      validator(val) {
        return ['click', 'scroll'].includes(val)
      }
    },
    autoHideScroller: {
      type: Boolean,
      default: false
    },
    autoHidePage: {
      type: Boolean,
      default: false
    },
    height: {
      type: [String, Number],
      default: 'auto'
    }
  },

  data() {
    this.pageHeight = 44 // TODO: 

    return {
      stateItem: [],
      pageData: {
        size: 0,
        length: 0,
        current: 1,
        total: 0
      },
      arrowOfMoreDisplay: true, // 
      loadingListData: false, // 
      pageDetail: { // 
        top: 0,
        left: 0,
        bottom: 0
      }
    }
  },

  computed: {
    cPrefix() { // 
      return `${this.compPrefix}-list`
    },
    pagerDisplay() { // 
      return this.pageData.total !== 0 &&
        this.pageData.current !== this.pageData.total
    },
    isPageTypeMore() { // 
      return this.pageType === PAGE_TYPE_MORE
    }
  },

  watch: {
    item(val) {
      if (this.auto) {
        this.setPageData({
          current: this.pageData.current
        })
      }

      this.setListItem({
        pageNum: this.pageData.current,
        pageSize: this.pageData.size,
        listItem: this.item
      })
    }
  },

  methods: {
    _setDataOpt() {
      this.pageData = {
        ...this.page
      }
    },

    _binder() {
      const refScroller = this.$refs.scroller

      refScroller.$on('scrollY', ({
        box
      }) => {
        if (this.pageTrigger === 'scroll') {
          if (box.position.top - box.offset.top < this.pageHeight && this.pageData.current + 1 <= this.pageData.total) {
            return this.switchPage({
              currentPage: this.pageData.current + 1
            })
          }
        }
      })

      refScroller.$on('change', (opt) => {
        return this.$emit('scrollerChange', {
          ...opt,
          emitter: this
        })
      })

      refScroller.$on('yBarChange', ({
        isBottom
      }) => {
        if (!this.$el.offsetHeight) {
          return false
        }
      })
    }
  },

  created() {
    this.initPageData()

    this.setListItem({
      pageNum: this.pageData.current,
      pageSize: this.pageData.size,
      listItem: this.item
    })
  }
}
