/**
 * 
 */

import Vue from 'vue'
import VueI18n from 'vue-i18n'

const set = {
  /**
   * 
   *
   * @param {string} lang -  i18n 
   */
  lang(langConfig) {
    Vue.use(VueI18n)

    const lang = Object.keys(langConfig)[0]

    const i18n = new VueI18n({
      locale: lang,
      messages: langConfig
    })

    return i18n
  }
}

export {
  set
}
