/**
 * page 
 *
 * @prop auto -data  length  size
 * @prop display - 
 * @prop data - 
 *             length - 
 *             total - 
 *             size - 
 *             current - 
 * @prop onePageDisplay -  1 
 * @prop size - s | m | l
 * @prop type - more | num
 * @prop loadMoreText - 
 *
 * @event switch - 
 *
 * @slot loadMore - 
 *
 */

import './Page.scss'

import Btn from '../Btn/Btn'
import Icon from '../Icon/Icon'
import Input from '../Input/Input'
import Row from '../Row/Row'
import Column from '../Col/Col'

import baseMixin from '../../mixin/base'
import render from './Page.render'

const pageComp = {
  name: 'Page',

  render,

  mixins: [baseMixin],

  components: {
    btn: Btn,
    icon: Icon,
    row: Row,
    column: Column,
    'input-box': Input
  },

  props: {
    auto: {
      type: Boolean,
      required: false
    },
    data: {
      type: Object,
      required: true
    },
    display: {
      type: Boolean,
      default: true
    },
    loadMoreText: {
      type: String,
      default: ''
    },
    onePageDisplay: {
      type: Boolean,
      default: false
    },
    size: {
      type: String,
      default: 'm'
    },
    type: {
      type: String,
      default: 'num'
    }
  },

  data() {
    return {
      pageData: {}, // 
      pageItem: [] // 
    }
  },

  computed: {
    cPrefix() { // 
      return `${this.compPrefix}-page`
    },
    moreDisplay() {
      return this.type === 'more'
    },
    numDisplay() {
      return this.type === 'num'
    },
    nextDisplay() {
      if (this.pageData.current === this.pageData.total) {
        return true
      }

      return false
    },
    preDisplay() {
      if (this.pageData.current === 1) {
        return true
      }

      return false
    },
    pageDisplay() {
      return this.display && (this.onePageDisplay || this.pageData.total > 1)
    }
  },

  watch: {
    data(val) {
      this._initPage(Object.assign({}, val))
    }
  },

  methods: {
    /**
     * 
     */
    _initPage(pageData) {
      if (this.auto) {
        Object.assign(pageData, {
          total: Math.ceil(pageData.length / pageData.size),
          current: 1
        })
      }

      let pageStart = 1
      let pageEnd = pageData.total
      let pageItem = []

      if (pageData.total >= 11) {
        if (pageData.current > 5 && pageData.current < pageData.total - 4) {
          pageStart = pageData.current - 5
          pageEnd = pageData.current + 4
        } else {
          if (pageData.current <= 5) {
            pageStart = 1
            pageEnd = 10
          } else {
            pageEnd = pageData.total
            pageStart = pageData.total - 9
          }
        }
      }

      while (pageStart <= pageEnd) {
        pageItem.push(pageStart)
        pageStart++
      }

      this.pageData = Object.assign(pageData, {
        item: pageItem
      })
    },

    /**
     * 
     */
    more(event = {}) {
      event.stopPropagation && event.stopPropagation()

      this.next()
    },

    /**
     * @param {Number} - 
     * @return {Function}
     */
    click(event = {}, currentPage) {
      event.stopPropagation && event.stopPropagation()

      if (currentPage === this.pageData.current) {
        return false
      }

      return this.switch(currentPage)
    },

    /**
     * 
     */
    next(event = {}) {
      event.stopPropagation && event.stopPropagation()

      if (this.pageData.current + 1 > this.pageData.total) {
        return false
      }

      return this.switch(this.pageData.current + 1)
    },

    /**
     * 
     */
    pre(event = {}) {
      event.stopPropagation && event.stopPropagation()

      if (this.pageData.current - 1 === 0) {
        return false
      }

      return this.switch(this.pageData.current - 1)
    },

    /**
     * 
     */
    end(event = {}) {
      event.stopPropagation && event.stopPropagation()

      return this.switch(this.pageData.total)
    },

    /**
     * 
     */
    start(event = {}) {
      event.stopPropagation && event.stopPropagation()

      return this.switch(1)
    },

    /**
     * 
     */
    jump(event = {}) {
      event.stopPropagation && event.stopPropagation()

      return this.switch(this.$refs.jumpInput.val())
    },

    /**
     * 
     */
    switch (pageNum) {
      if (isNaN(pageNum)) {
        return false
      }

      this.pageData.current = pageNum

      return this.$emit('switch', {
        currentPage: pageNum,
        emitter: this
      })
    }
  },

  created() {
    this._initPage(Object.assign({}, this.data))
  }
}

export default pageComp
