/**
 * check - 
 *
 * @prop checkAll -  checkbox 
 * @prop checkAllLabel -  checkbox  label
 * @prop checkAllDisabled -  checkbox 
 * @prop multiple - 
 * @prop option - 
 *              ex: [{
 *                    value: 1,
 *                    text: 'a',
 *                    disabled: true //  false
 *                  }]
 * @prop param - 
 * @prop required - 
 * @prop textName -  checkboxItems  text  key 
 * @prop value -  checkbox 
 * @prop valueName -  checkboxItems  value  key 
 * @prop vertical -  false
 * @prop verifiedHint - checkbox  form 
 *
 * @event click - 
 * @event change - 
 *
 * @method check - 
 * @method verify - 
 */

import './Check.scss'
import './Check.material.scss'
import './Check.bootstrap.scss'

import render from './Check.render'

import Col from '../Col/Col'
import Row from '../Row/Row'
import Icon from '../Icon/Icon'
import MotionRip from '../MotionRip/MotionRip'

import baseMixin from '../../mixin/base'
import formMixin from '../../mixin/form'
import apiMixin from './Check.method.js'

import {
  isEmpty as isEmptyArray
} from '../../util/data/array'

let checkCompConfig = {
  name: 'Check',

  mixins: [baseMixin, formMixin, apiMixin],

  render,

  components: {
    column: Col,
    row: Row,
    icon: Icon,
    'motion-rip': MotionRip
  },

  props: {
    checkAll: {
      type: Boolean,
      default: false
    },
    checkAllLabel: {
      type: String,
      default: ''
    },
    checkAllDisabled: {
      type: Boolean,
      default: false
    },
    multiple: {
      type: Boolean,
      default: false
    },
    option: {
      type: Array,
      default: () => [],
      validator(val) {
        return Array.isArray(val)
      }
    },
    param: {
      type: String,
      default: ''
    },
    required: {
      type: Boolean,
      default: false
    },
    textName: {
      type: String,
      default: 'text'
    },
    value: [Number, Array],
    valueName: {
      type: String,
      default: 'value'
    },
    verifiedHint: {
      type: String,
      default: ''
    },
    vertical: {
      type: Boolean,
      default: false
    }
  },

  data() {
    return {
      compName: 'check', // 
      index: {}, //  index([])(-1) TODO: index 
      oldIndex: {},
      stateValue: {}, // check  value 
      text: {}, // check  text ,  [],  'undefined'
      stateOption: [], // check 
      oldValue: [], // check  value 
      verified: true, // 
      optionFocus: [], //  focus 
      focusedCheckAll: false, //  focus 
      allowFocus: true, //  focus 
      slotItems: []
    }
  },

  computed: {
    cPrefix() {
      return `${this.compPrefix}-check`
    },
    isCheckbox() {
      return this.multiple
    },
    isRadio() {
      return !this.multiple
    },
    checkedAll() { // 
      if (this.checkAll && this.multiple) {
        return this.index.length === this.stateOption.length
      }
    },
    checkedSome() { // 
      if (this.checkAll && this.multiple) {
        return this.index.length < this.stateOption.length && this.index.length > 0
      }
    },
    checkIconName() {
      switch (this.ui) {
        case 'bootstrap':
          return {
            radio: {
              uncheck: 'circle',
              checked: 'circle-check'
            },
            checkbox: {
              uncheck: 'square-bs',
              checked: 'square-check-bs',
              indeterminate: 'square-indeterminate-bs'
            }
          }
        case 'material':
        default:
          return {
            radio: {
              uncheck: 'circle-o',
              checked: 'circle-check-o'
            },
            checkbox: {
              uncheck: 'square-o',
              checked: 'square-check'
            }
          }
      }
    }
  },

  watch: {
    value(val) {
      this.stateValue = val
      this._initCheckbox()
    },
    option(val) {
      this.stateOption = val
      this._initCheckbox()
    },
    stateValue(val) {
      this.$emit('change', {
        value: val,
        index: this.index
      })
    }
  },

  methods: {
    /**
     *  data 
     */
    _setDataOpt() {
      if (this.value === undefined) {
        this.stateValue = this.isCheckbox ? [] : -1
      } else {
        this.stateValue = this.isCheckbox ? this.value.slice() : this.value
      }

      this.index = this.isCheckbox ? [] : undefined
      this.text = this.isCheckbox ? [] : undefined

      this.stateOption = Object.assign([], this.option)
      this.optionFocus = this.option.map(() => {
        return false
      })
    },

    /**
     * 
     *
     * @param {Boolean} checked - 
     * @param {Boolean} multiple - 
     */
    _getIconName(checked = false, indeterminate = false, multiple = this.multiple) {
      if (multiple) {
        if (indeterminate) {
          return this.checkIconName.checkbox.indeterminate
        }

        return checked ? this.checkIconName.checkbox.checked :
          this.checkIconName.checkbox.uncheck
      } else {
        return checked ? this.checkIconName.radio.checked :
          this.checkIconName.radio.uncheck
      }
    },

    /**
     * checkbox
     **/
    _initCheckbox() {
      this._setIndex()
      this._setText()

      this.verified = !this.required || (this.isCheckbox ? this.stateValue.length !== 0 : this.stateValue !== 'undefined')
    },

    /**
     * checkboxItems
     *
     * @return {Function, Object}
     **/
    _initCheckboxItems() {
      if (!this._slotContents && !(!!this.$options._content && this.$options._content.innerHTML)) {
        return false
      }

      let $checkboxSlot = {}
      let optionContent = this.$options._content ? this.$options._content.innerHTML : ''
      let $checkboxItemSlot = $(this.$el).find('.checkbox-items-slot')

      if (optionContent) {
        $checkboxSlot = $checkboxItemSlot.html(optionContent)
      } else {
        console.warn('vm.$options._content , ')
        $checkboxSlot = $checkboxItemSlot.html(this._slotContents.default)
      }

      let $checkEles = $checkboxSlot.find('check-ele')

      if ($checkEles.length === 0) {
        return this
      }

      let items = []
      let checkboxItemsEmpty = isEmptyArray(this.stateOption)

      $checkEles.each((index, el) => {
        let $el = $(el)
        let val = $el.attr('value')
        let txt = ''

        val = isNaN(val) ? val : Number(val)

        if ($el[0].hasAttribute('text')) {
          txt = $el.attr('text').trim()

          //  html  text 
          this.slotItems.push($el.html().trim())
        } else {
          txt = $el.text().trim()
        }

        if (checkboxItemsEmpty) {
          items.push({
            value: val,
            text: txt
          })
        }
      })

      $checkboxItemSlot.html('')
      checkboxItemsEmpty && this.$set('checkboxItems', items)

      this.$nextTick(() => {
        this._initCheckboxSlot()
      })

      return this
    },

    /**
     * checkboxItems  slot
     */
    _initCheckboxSlot() {
      if (this.slotItems.length === 0) {
        return false
      }

      if (typeof this.compileVm === 'undefined') {
        this.compileVm = this.$parent
      }

      $(this.$el).find(`.${this.cPrefix}-opt-slot .item`).each((index, el) => {
        if (this.slotItems[index]) {
          let dom = document.createElement('div')

          dom.innerHTML = this.slotItems[index]
          this.compileVm.$compile(dom)
          el.appendChild(dom.firstChild)
        }
      })
    },

    /**
     *  checkbox  value 
     *
     * @param {Number} - checkbox 
     * @param {String, Number} - checkbox 
     */
    _changeCheckbox(index, val) {
      let hasDelflag = false

      this.stateValue.every((item, index) => {
        if (val === item) {
          hasDelflag = true

          this.stateValue.splice(index, 1)
          this.index.splice(index, 1)

          return false
        }

        return true
      })

      if (hasDelflag) {
        return this
      }

      this.stateValue.push(val)
      this.index.push(index)
    },

    /**
     * 
     *
     * @param {Object} event
     * @param {Number} index
     */
    async _check(event, index) {
      let option = this.stateOption[index - 1]
      let val = option[this.valueName]

      if (this.isCheckbox) {
        this.oldValue = []

        this.stateValue.forEach((item) => {
          this.oldValue.push(item)
        })

        this._changeCheckbox(index - 1, val)
      } else {
        this.oldValue = this.stateValue

        this.stateValue = val
      }

      this._initCheckbox()
      await this.$nextTick()

      this.$emit('click', {
        index,
        event,
        value: this.value
      })

      this.UIMaterial && this.$refs[`motionCheck${index}`].enter()
    },

    /**
     * 
     *
     * @return {Object} - this - 
     */
    async _checkAllOption() {
      let value = []

      this.option.forEach((item) => {
        value.push(item[this.valueName])
      })

      if (this.checkedAll) {
        this.stateValue = []
      } else {
        this.stateValue = value
      }

      this._initCheckbox()

      await this.$nextTick()

      this.UIMaterial && this.$refs.motionCheckAll.enter()
    },

    /**
     * checkboxtext
     *
     * @return {Function, String}
     **/
    _setText() {
      if (this.isRadio) {
        this.oldText = this.text

        this.text = this.index === -1 ?
          'undefined' :
          this.stateOption[this.index][this.textName]

        return this
      } else {
        this.oldText = this.text.slice()
        let checkboxText = []

        this.index.forEach((item) => {
          checkboxText.push(this.stateOption[item][this.textName])
        })

        this.text = checkboxText
      }
    },

    /**
     *  currentIndex
     *
     * @return {Function, Object}
     **/
    _setIndex() {
      if (this.isRadio) {
        this.oldIndex = this.index

        return this.stateOption.every((item, index) => {
          if (item[this.valueName] === this.stateValue) {
            this.index = index

            return false
          }

          return true
        })
      } else {
        this.oldIndex = this.index.slice()
        let checkboxIndex = []

        this.stateValue.forEach((item) => {
          this.stateOption.forEach((ele, index) => {
            if (item === ele[this.valueName]) {
              checkboxIndex.push(index)
            }
          })
        })

        this.index = checkboxIndex
      }
    },

    /**
     * click 
     */
    _handlerClick(event, index) {
      return this._check(event, index)
    },

    /**
     * Mousedown 
     */
    _handlerMousedown(event, index) {
      if (this.inTouch) {
        return false
      }

      this.allowFocus = false
    },

    /**
     * Mouseup 
     */
    _handlerMouseup(event, index) {
      if (this.inTouch) {
        return false
      }

      this.allowFocus = true
    },

    /**
     * Keyup 
     */
    _handlerKeyup(event, index) {
      if (event.keyCode === 13) {
        return this._check(event, index)
      }
    },

    /**
     * focus 
     */
    _handlerFocus(event, index) {
      if (this.allowFocus) {
        this.optionFocus.splice(index - 1, 1, true)
      }
    },

    /**
     * blur 
     */
    _handlerBlur(event, index) {
      this.optionFocus.splice(index - 1, 1, false)
    },

    /**
     *  Mousedown 
     */
    _handlerMousedownCheckAll(event) {
      if (this.inTouch) {
        return false
      }

      this.allowFocus = false
    },

    /**
     *  Mouseup 
     */
    _handlerMouseupCheckAll(event) {
      if (this.inTouch) {
        return false
      }

      this.allowFocus = true
    },

    /**
     *  focus 
     */
    _handlerFocusCheckAll(event) {
      if (this.allowFocus) {
        this.focusedCheckAll = true
      }
    },

    /**
     *  blur 
     */
    _handlerBlurCheckAll(event) {
      this.focusedCheckAll = false
    },

    /**
     *  keyup 
     */
    _handlerKeyupCheckAll(event) {
      if (event.keyCode === 13) {
        return this._checkAllOption()
      }
    }
  },

  created() {
    this._initCheckboxItems()
    this._initCheckbox()
  }
}

export default checkCompConfig
