/**
 * col 
 *
 * @prop gap - px (5, 10, 20, 30, 40, 50)
 * @prop pull -  x 
 * @prop push -  x 
 * @prop offset - 
 * @prop span -  12 
 * @prop width -  %  px  span 
 * @prop xs - 
 * @prop s - 
 * @prop m - 
 * @prop l - 
 * @prop xl - 
 * @prop grid - 
 * @prop grow - (draft) flex-grow
 * @prop shrink - (draft) flex-shrink
 */

// import './Col.scss' // Col.scss 
import render from './Col.render.js'
import baseMixin from '../../mixin/base'

export default {
  name: 'Col',

  mixins: [baseMixin],

  render,

  props: {
    pull: {
      type: Number,
      default: 0
    },
    push: {
      type: Number,
      default: 0
    },
    offset: {
      type: Number,
      default: 0
    },
    span: {
      type: [Number, String],
      default: 0,
      validator(val) {
        if (typeof val === 'number') {
          return true
        } else if (val.includes('px')) {
          return true
        } else {
          return false
        }
      }
    },
    width: {
      type: String,
      validator(val) {
        return val.includes('px') || val.includes('%')
      }
    },
    xs: {
      type: Number,
      default: 0
    },
    s: {
      type: Number,
      default: 0
    },
    m: {
      type: Number,
      default: 0
    },
    l: {
      type: Number,
      default: 0
    },
    xl: {
      type: Number,
      default: 0
    },
    grid: Object,
    grow: {
      type: Number,
      default: 0
    },
    shrink: {
      type: Number,
      default: 0
    }
  },

  computed: {
    cPrefix() { // 
      return `${this.compPrefix}-col`
    },
    compStyle() {
      return {
        width: this.width,
        'flex-grow': this.grow === 0 ? undefined : this.grow
      }
    }
  }
}
