/**
 * modal api
 */

export default {
  methods: {
    /**
     *  Full 
     */
    clickFullNav() {
      if (this.commit) {
        this.no()
      } else {
        this.hide()
      }
    },

    /**
     * pop
     *
     * @param {Number} - 
     * @return {Object}
     */
    show() {
      this.modalDisplay = true

      return this.$nextTick(() => {
        this.$refs.fadeTransition.enter()
        this.$refs.pop.show()

        return this
      })
    },

    /**
     * pop
     *
     * @return {Object}
     */
    hide() {
      this.$refs.fadeTransition.leave()

      this.$refs.pop.hide({
        cb: () => {
          this.modalDisplay = false
          this.isMousedown = false
        }
      })

      return this
    },

    /**
     * mouseDown 
     *
     * @return {Object}
     */
    mouseDown(event) {
      this.isMousedown = true

      this.pointStart = {
        x: event.clientX,
        y: event.clientY
      }

      return this
    },

    /**
     * mouseMove 
     *
     * @return {Object, Boolean}
     */
    mouseMove(event) {
      event.preventDefault()

      if (!this.isMousedown) {
        return false
      }

      let $this = this.$el.querySelector('.' + this.xclass('pop'))
      let styleHub = getComputedStyle($this)
      let top = parseFloat(styleHub.top, 10)
      let left = parseFloat(styleHub.left, 10)

      this.$refs.pop.computePosition()

      this.pointStart = {
        x: event.clientX,
        y: event.clientY
      }

      return this
    },

    /**
     * mouseUp 
     *
     * @return {Object, Boolean}
     */
    mouseUp(event) {
      event.preventDefault()

      if (!this.isMousedown) {
        return false
      }

      this.isMousedown = false

      return this
    },

    /**
     * 
     *
     * @return {Object}
     */
    ok() {
      this.$emit('ok')

      if (this.okCbFun) {
        if (typeof this.okCbFun === 'function') {
          this.okCbFun(this)
        }

        return this
      }

      return this.hide()
    },

    /**
     * 
     *
     * @return {Object}
     */
    no() {
      this.$emit('no')

      if (this.noCbFun) {
        if (typeof this.noCbFun === 'function') {
          this.noCbFun(this)
        }

        return this
      }

      this.hide()
    },

    /**
     *  /  title
     *
     * @return {Object, Boolean}
     */
    title(text) {
      if (text === '' || text) {
        this.stateHeader = text
      }

      return this
    },

    /**
     * alert, confirm 
     *
     * @param {String} - 
     * @return {Object, String}
     */
    info(text) {
      if (text === '' || text) {
        this.stateMessage = text
      }

      return this
    },

    /**
     * 
     *
     * @param {Object} opt - 
     *                       {Function} okCb - 
     *                       {Function} noCb - 
     *                       {Function} showCb - 
     *                       {Function} hideCb - 
     *                       {String} title - 
     *                       {Function} message - 
     */
    set({
      okCb,
      noCb,
      showCb,
      hideCb,
      title = '',
      message = '',
      ui = this.ui,
      theme = this.theme
    } = {}) {
      this.okCbFun = okCb
      this.noCbFun = noCb
      this.showCb = showCb
      this.hideCb = hideCb
      this.stateHeader = title
      this.stateMessage = message

      this.stateUI = ui
      this.stateTheme = theme

      return this
    }
  }
}
