/**
 * menu.api
 */

import keyCode from '../../config/keyCode.json'
import tip from '../Message/tip'

export default {
  methods: {
    /**
     *  css  class 
     */
    defaultValClassName(value) {
      return this.defaultVal === value ? `${this.cPrefix}-default-text` : ''
    },

    /**
     * 
     *
     */
    clickParent() {
      if (this.panelDisplay) {
        return this._togglePanelDisplay(false)
      }
    },

    /**
     * 
     *
     * @return {Object} this - 
     */
    blur() {
      this.focusing = false
    },

    /**
     * 
     *
     * @return {Object} this - 
     */
    focus() {
      this.focusing = true
    },

    /**
     * 
     *
     * @return {Object} this - 
     */
    click(event) {
      event.stopPropagation()

      return this._togglePanelDisplay()
    },

    /**
     * keydown
     */
    keydown(event) {
      if (!this.focusing) {
        return false
      }

      if (event.keyCode === keyCode.enter) {
        this._togglePanelDisplay()
      }
    },

    /**
     * 
     * @return {Object} this - 
     */
    spread() {
      return this._togglePanelDisplay(true)
    },

    /**
     * 
     * @return {Object} this - 
     */
    fold() {
      return this._togglePanelDisplay(false)
    },

    /**
     * / 
     * @return {Object} this - 
     */
    toggle() {
      return this._togglePanelDisplay()
    },

    /**
     * 
     */
    adjust(opt = {}) {
      this.menuHeight = opt.scrollerHeight
      this.UIMaterial && this.$refs.motion.adjustMotion()
    }
  }
}
