/**
 * 
 * 
 * 
 *
 * ex: Markdown, 
 *
 * @param {Object} func - 
 * @param {Number} wait -  1000 
 */
const debounce = function (func, wait = 1000) {
  let timeout = null

  const debounced = function () {
    clearTimeout(timeout)

    timeout = setTimeout(() => {
      func(...arguments)
    }, wait)
  }

  debounced.cancel = function () {
    clearTimeout(timeout)

    timeout = null
  }

  return debounced
}

/**
 * 
 * 
 * 
 *
 * @param {Object} func - 
 * @param {Number} wait - 
 */
const throttle = function (func, wait = 1000) {
  let startTime = Date.now()

  const throttled = function () {
    let time = Date.now()

    if (startTime + wait - time <= 0) {
      startTime = time

      func(...arguments)

      return true
    }

    return false
  }

  throttled.cancel = () => {
    startTime = Date.now()
  }

  return throttled
}

export {
  debounce,
  throttle
}
