/**
 * menu 
 *
 * @prop classify -  option 
 * @prop classifyOpt - 
 * @prop coverTrig - 
 * @prop defaultValue - 
 * @prop defaultText - 
 * @prop errorMessage - 
 * @prop max - 
 * @prop min - 
 * @prop menuWidth - 
 * @prop multiple - 
 * @prop option -  option 
 * @prop param - 
 * @prop required - 
 * @prop readOnly - 
 * @prop search - 
 * @prop selectAll - 
 * @prop selectAllTxt - 
 * @prop store - 
 * @prop textName -   optionItems  text  key 
 * @prop value - 
 * @prop valueName -  optionItems  value  key 
 *
 * @event change - 
 */

import '../../lib/directive/clickParent'
import '../../scss/common/main.scss'
import '../../scss/common/box.scss'
import './Select.scss'
import './Select.bootstrap.scss'
import './Select.material.scss'

import SelectOpt from './SelectOpt'
import keyCode from '../../config/keyCode.json'

import render from './Select.render'
import store from '../../vuex/store'
import compStore from '../../vuex/module/comp/type.json'

import Icon from '../Icon/Icon'
import Input from '../Input/Input'
import Check from '../Check/Check'
import Scroller from '../Scroller/Scroller'

import Menu from '../Menu/Menu'

import baseMixin from '../../mixin/base'
import formMixin from '../../mixin/form'
import apiMixin from './Select.api'

import {
  handleEleDisplay
} from '../../util/dom/prop'

import uid from '../../util/uid'

// 
const SEARCH_KEY_UP_INTERVAL = 500
const MENU_WIDTH = 170

export default {
  name: 'Select',

  render,

  mixins: [baseMixin, formMixin, apiMixin],

  store,

  components: {
    'menu-comp': Menu,
    'select-opt': SelectOpt,
    'input-box': Input,
    icon: Icon,
    check: Check,
    scroller: Scroller
  },

  props: {
    classify: Array,
    classifyOpt: Object,
    coverTrig: {
      type: Boolean,
      default: false
    },
    defaultValue: {
      type: [Number, String],
      default: -1
    },
    defaultText: {
      type: [Number, String],
      default: ''
    },
    errorMessage: {
      type: String,
      default: ''
    },
    multiple: {
      type: Boolean,
      default: false
    },
    max: {
      type: Number,
      default: 0
    },
    min: {
      type: Number,
      default: 0
    },
    menuWidth: {
      type: [String, Number],
      default: 170,
      validator(val) {
        if (typeof val === 'number') {
          return true
        } else if (val === 'auto' || val === '100%') {
          return true
        } else {
          return false
        }
      }
    },
    option: {
      type: Array,
      default: () => []
    },
    param: {
      type: String,
      default: ''
    },
    required: {
      type: Boolean,
      default: false
    },
    value: [Number, Array, String],
    valueName: {
      type: String,
      default: 'value'
    },
    textName: {
      type: String,
      default: 'text'
    },
    readOnly: {
      type: Boolean,
      default: false
    },
    selectAll: {
      type: Boolean,
      default: false
    },
    search: {
      type: Boolean,
      default: false
    },
    store: Object,
    selectAllTxt: {
      type: String,
      default: ''
    }
  },

  data() {
    this.compName = 'select' // 
    this.uid = '' // 
    this.togglingMenu = false // 300ms 

    return {
      allOptionVal: [], // optionItem  value
      currentIndex: 0, // option 
      customOptionDisplay: false, // 
      focusing: false, //  focus 
      hasSlotOption: false, //  slot  option
      menuDisplay: false, // 
      optionItemCopy: {}, //  classify  option 
      searchKeyuped: false, // 
      searchOptionDisplay: false, //  optionItem
      searchOptionItem: {}, //  option
      selectedAll: false, // 
      selectedHeight: 0, // 
      selectedStyleHeight: 0, // 
      stateCoverTrig: false, // 
      stateMenuHeight: 0, // 
      stateMenuWidth: this._getMenuWidth(this.menuWidth), // 
      stateOption: [], // props  optionItem  data 
      stateValue: undefined, //  value 
      transitionFinish: false, // 
      text: undefined, //  text 
      unwatchOption: {}, //  option
      verified: true, // 
      verifiedHint: '' // 
    }
  },

  computed: {
    cPrefix() { // 
      return `${this.compPrefix}-select`
    },
    me() {
      return this
    },
    selectClass() { //  stage  class 
      let classArr = [
        this.cPrefix,
        this.xclass(this.compClass),
        {
          [this.xclass('selecting')]: this.menuDisplay
        },
        {
          [this.xclass('focusing')]: this.focusing
        },
        {
          [this.xclass('multiple')]: this.multiple
        }
      ]

      return classArr
    },
    isCustomOption() { // 
      return this.option.length > 0 && this.customOptionDisplay
    },
    initTxtDisplay() { // 
      return this.multiple && this.stateValue.length === 0
    }
  },

  watch: {
    stateValue(val) {
      if (this.multiple && this.initTxtDisplay) { // 
        return this.$nextTick(() => this._adjustSelectedPoiStyle(''))
      }

      if (this.multiple && this.selectAll) {
        this.selectedAll = val.length > 0 && val.length === this.allOptionVal.length
      }

      return this._initSelectTxt()
    },
    value(val) {
      this.stateValue = this.multiple ? val.slice() : val
    },
    option(val) {
      return this._processOption(val.slice())
    },
    classifyOpt(val) {
      return this._processOption(val)._initAllOptionVal()._initSelectTxt()
    },
    deviceSize(val) {
      this.changeByDeviceSize(val)
    },
    selectedHeight(val) {
      this._adjustMenuMotion()
    },
    menuWidth(val) {
      this.stateMenuWidth = this._getMenuWidth(val)
    }
  },

  methods: {
    _initComp() {
      this._adjustSelectedPoiStyle()
    },

    _initDataOpt() {
      this.stateCoverTrig = this.coverTrig
    },

    // 
    _getMenuWidth(width) {
      const triggerWidth = this.$el ? this.$el.offsetWidth : 0

      if (typeof width === 'number') {
        return triggerWidth > width ? triggerWidth : width
      } else if (width === '100%') {
        return triggerWidth
      } else {
        return width
      }
    },

    /**
     * 
     */
    _binder() {
      if (this.$refs.scroller) {
        this.$refs.scroller.$off('change')
        this.$refs.scroller.$on('change', ({
          scrollerHeight
        }) => {
          // 
          if (!this.initTxtDisplay) {
            return this._adjustSelectedPoiStyle(scrollerHeight + 16)
          }
        })
      }

      this.$refs.option.$off('change')
      this.$refs.option.$on('change', ({
        value,
        text,
        index,
        hideMenu
      }) => {
        this.currentIndex = index
        let selectedItem = this._isExistedVal(value)

        if (this.multiple) {
          if (!selectedItem) {
            if (this.max === 0 || this.stateValue.length !== this.max) {
              this.stateValue.push(value)
            }
          } else {
            this.removeMultiSelected(selectedItem.index + 1)
          }
        } else {
          this.stateValue = value

          hideMenu && this._menuMotion(false)
        }

        return this.$emit('change', {
          value,
          text,
          index
        })
      })
    },

    /**
     * 
     */
    _adjustSelectedPoiStyle(height, cb) {
      const refSelected = this.$refs.selected

      if (height === undefined) {
        const selectedHeight = refSelected.offsetHeight

        this.selectedStyleHeight = selectedHeight + 'px'
        this.selectedHeight = selectedHeight
      } else if (height === '') {
        refSelected.style.height = ''
        this.selectedHeight = refSelected.offsetHeight
      } else {
        this.selectedStyleHeight = height + 'px'
        this.selectedHeight = height
      }

      cb && cb()
    },

    /**
     *  data 
     */
    _setDataOpt() {
      if (this.value) {
        this.stateValue = this.multiple ? this.value.slice() : this.value
      }

      this.stateOption = this.option.slice()
    },

    /**
     *  allOptionVal
     */
    _initAllOptionVal() {
      let value = []
      let optionTemp = this.classify ? this.stateOptionItemCopy : this.stateOption

      optionTemp.forEach((item) => {
        value.push(item[this.valueName])
      })

      this.allOptionVal = value

      return this
    },

    /**
     *  option
     */
    _optionion() {
      if (this.classifyOpt) {
        return this._processOption(this.classifyOpt)._initAllOptionVal()._initSelectTxt()
      } else {
        let slotOption = this._initSelectSlot()

        if (slotOption) {
          this.stateOption = slotOption
        }

        return this._processOption(this.stateOption.slice())._initAllOptionVal()._initSelectTxt()
      }
    },

    /**
     *  slot  option
     *
     * @return { Array } optionItem -  slot  option
     */
    _initSelectSlot() {
      const $defaultSlotContent = this.$slots.default

      // slot default 
      if (!Array.isArray($defaultSlotContent) || $defaultSlotContent.length === 0) {
        return false
      }

      this.hasSlotOption = true
      let optionItem = []

      $defaultSlotContent.forEach((item) => {
        if (item.text === ' ') {
          return false
        }

        let children = item.componentOptions &&
          Array.isArray(item.componentOptions.children) &&
          item.componentOptions.children[0]

        if (!children) {
          return false
        }

        let attrs = item.data ? item.data.attrs : {}
        let text = attrs.text === undefined ? (children ? (children.text ? children.text : '(empty)') : '') : attrs.text

        optionItem.push({
          value: attrs.value,
          text
        })
      })

      return optionItem
    },

    /**
     * 
     */
    _initSelectTxt() {
      if (this.multiple) {
        this._initMultipleSelectTxt()
      } else {
        this._initSingleSelectTxt()
      }

      return this
    },

    /**
     *  
     */
    _initMultipleSelectTxt() {
      if (!Array.isArray(this.stateOption)) {
        return this
      }

      if (!Array.isArray(this.stateValue)) {
        console.error(` "this.stateValue" !!`)
        this.stateValue = []

        return false
      }

      let valueTemp = this.stateValue
      let optionTemp = this.stateOption
      let toBeText = []

      valueTemp.forEach((ele, index) => {
        optionTemp.every((item, itemIndex) => {
          if (item[this.valueName] === ele) {
            toBeText.push(item[this.textName])

            return false
          }

          return true
        })
      })

      return this._setTxtVal({
        text: toBeText,
        replace: true
      })
    },

    /**
     * 
     */
    _initSingleSelectTxt(val, txt) {
      if (!Array.isArray(this.stateOption)) {
        return this
      }

      if (this.stateValue || this.stateValue === 0 || this.stateValue === '0') {
        this.stateOption.every((ele, index) => {
          if (ele[this.valueName] === this.stateValue) {
            this._setTxtVal({
              value: ele[this.valueName],
              text: ele[this.textName]
            })

            return false
          }

          return true
        })

        return this
      }

      if (typeof this.stateOption[0] === 'object') {
        this._setTxtVal({
          value: this.stateOption[0][this.valueName],
          text: this.stateOption[0][this.textName]
        })
      }

      return this
    },

    /**
     *  value 
     *
     * @param {String, Number} - 
     */
    _isExistedVal(val) {
      if (!this.multiple) {
        return false
      }

      var isExisted = false
      var existItem = {}

      this.stateValue.every((selectedVal, index) => {
        if (val === selectedVal) {
          isExisted = true
          existItem = {
            value: selectedVal,
            index: index
          }

          return false
        }

        return true
      })

      if (isExisted) {
        return existItem
      } else {
        return false
      }
    },

    /**
     *  text  value
     */
    _setTxtVal({
      value,
      text,
      replace = false
    }) {
      if (!this.multiple || replace) {
        if (value !== undefined) {
          this.stateValue = value
        }

        if (text !== undefined) {
          this.text = text
        }

        return this
      }

      if (Array.isArray(value)) {
        value.length > 0 && this.stateValue.concat(value)
      } else {
        text !== undefined && this.text.push(text)
      }

      if (Array.isArray(text)) {
        value.length > 0 && this.stateValue.concat(value)
      } else {
        value !== undefined && this.stateValue.push(value)
      }

      return this
    },

    /**
     *  input 
     */
    _searchKeyup(evt) {
      let keyWord = evt.target.value

      if (!keyWord && keyWord !== 0) {
        this.searchOptionDisplay = false

        return false
      }

      this.searchKeyuped = true

      setTimeout(() => {
        this.searchKeyuped = false
      }, SEARCH_KEY_UP_INTERVAL)

      this.searchOptionDisplay = true
      let realOptionItem = this.stateOption

      if (this.classify || this.classifyOpt) {
        realOptionItem = this.stateOptionItemCopy
      }

      this.searchOptionItem = realOptionItem.filter(item => {
        return item[this.textName].indexOf(keyWord) > -1
      })

      if (this.searchOptionItem.length === 0) {
        this.searchOptionItem.push({
          [this.valueName]: `${this.compPrefix}-menu: search not found`,
          [this.textName]: '',
          classify: true
        })
      }
    },

    /**
     *   option
     */
    _watchOption() {
      this.unwatchOption = this.$watch('option', function (val, oldVal) {
        if (!this.hasSlotOption) {
          return this._processOption(val)._initAllOptionVal()._initSelectTxt()
        }
      })
    },

    /**
     * 
     *
     * @return {Object} this - 
     */
    _processOption(optionItem) {
      let toBeOption = []

      if (this.classify) {
        toBeOption = this._processClassifyOption(optionItem)
      } else {
        toBeOption = optionItem
      }

      this.stateOption = toBeOption

      return this
    },

    /**
     *  classify 
     *
     * @return {Array} optionTemp -  option
     */
    _processClassifyOption(optionItem) {
      let optionTemp = []
      let allOptionTemp = []
      let allOption = []

      this.classify.forEach((item) => {
        optionTemp = optionTemp.concat([{
          [this.valueName]: item.key,
          [this.textName]: item.text,
          classify: true
        }], optionItem[item.key])

        allOption = allOption.concat(optionItem[item.key])
      })

      for (let i = 0, len = allOption.length; i < len; i++) {
        for (let j = i + 1; j < len; j++) {
          if (allOption[i].value === allOption[j].value) {
            i++
          }
        }

        allOptionTemp.push(allOption[i])
      }

      allOption = allOptionTemp

      optionTemp = optionTemp.concat([{
        [this.valueName]: 'all',
        [this.textName]: '',
        classify: true
      }], allOption)

      this.stateOptionItemCopy = allOption

      return optionTemp
    },

    /**
     * 
     *
     * @param {Boolean} optVal - ,
     *                        false:  true: undefined 
     *
     * @return {Object} - this
     */
    _menuMotion(optVal = !this.menuDisplay, vm = this) {
      const getMenuData = (vm) => {
        handleEleDisplay({
          element: vm.$refs.menu.$refs.panel,
          cb: (element) => {
            let scrollerComp = vm.$refs.option.$refs.list.$refs.scroller
            scrollerComp.initScroller()

            vm.stateMenuHeight = scrollerComp.scrollerHeight
            // vm.stateMenuWidth = this._getMenuWidth(this.state)
          }
        })
      }

      const transite = (state, vm) => {
        if (state) {
          getMenuData(vm)

          vm.menuDisplay = true

          //  menu  height  menu 
          this.$nextTick(() => {
            vm.$refs.menu.spread()
          })
        } else {
          getMenuData(vm)

          vm.menuDisplay = false

          if (this.UIMaterial) {
            setTimeout(() => {
              vm.$refs.menu.fold()
            }, 300)
          } else {
            vm.$refs.menu.fold()
          }
        }
      }

      return transite(optVal, vm)
    },

    /**
     * 
     */
    _adjustMenuMotion() {
      if (this.menuDisplay) {
        return this.$refs.menu.adjust()
      }
    },

    /**
     * keydown
     */
    _handlerKeydown(event) {
      const $refOption = this.$refs.option

      if (!this.focusing) {
        return false
      }

      switch (event.keyCode) {
        case keyCode.enter:
          this.toggle()
          break
        case keyCode.up:
          $refOption.keydown('up')
          event.preventDefault()
          break
        case keyCode.down:
          $refOption.keydown('down')
          event.preventDefault()
          break
        case keyCode.left:
          $refOption.keydown('left')
          event.preventDefault()
          break
        case keyCode.right:
          $refOption.keydown('right')
          event.preventDefault()
          break
        default:
          break
      }
    }
  },

  created() {
    this.uid = uid()

    this.$store.dispatch(compStore.common.add, {
      vm: this,
      name: this.compName,
      id: this.uid
    })

    if (this.multiple) {
      this._setTxtVal({
        value: this.stateValue || [],
        text: [],
        replace: true
      })
    }

    this._optionion()
  },

  mounted() {
    if (this.$scopedSlots.custom) {
      this.customOptionDisplay = true
    }

    this.$nextTick(() => {
      this.stateMenuWidth = this._getMenuWidth(this.menuWidth)
    })
  },

  updated() {
    // TODO UI  option 
    this._binder()
  }
}
