/**
 * message 
 *
 * @prop message - 
 * @prop direction - 
 * @prop position - 
 * @prop type - (pop | bar | header)
 *
 * @slot - 
 *
 * @event show - 
 * @event hide - 
 */

import './Message.scss'
import './Message.m.scss'

import render from './Message.render'
import baseMixin from '../../mixin/base'

import Pop from '../Pop/Pop'

import {
  handleEleDisplay
} from '../../util/dom/prop'

const TIP_DISPLAY_TIME = 1500

const messageComp = {
  name: 'Message',

  render,

  mixins: [baseMixin],

  components: {
    pop: Pop
  },

  computed: {
    cPrefix() { // 
      return `${this.compPrefix}-message`
    },
    headerClass() { //  header  class 
      return {
        [`${this.cPrefix}-no-header`]: !this.headerDisplay,
        [`${this.cPrefix}-no-header-title`]: !this.popHeaderName
      }
    },
    footerClass() { //  footer  class 
      return {
        [`${this.cPrefix}-no-footer`]: !this.footerDisplay
      }
    }
  },

  props: {
    type: {
      type: String,
      default: 'pop'
    },
    direction: {
      type: String,
      default: 'south',
      validator(val) {
        return ['north', 'east', 'west', 'south'].includes(val)
      }
    },
    message: {
      type: String,
      default: ''
    },
    position: {
      type: String,
      default: 'center',
      validator(val) {
        return ['top', 'right', 'bottom', 'left', 'center'].includes(val)
      }
    }
  },

  data: () => {
    return {
      infoMessage: '', // 
      messageType: '', // 
      messageDisplay: false,
      hideCb: null
    }
  },

  methods: {
    _initmessage() {
      handleEleDisplay({
        element: this.$el,
        cb: () => {
          this.$refs.pop.computePosition()
        }
      })
    },

    /**
     * 
     */
    _setDataOpt() {
      this.infoMessage = this.message
    },

    /**
     * 
     *
     * @param {Object} opt - 
     *                       {Function} cb - 
     * @return {Promise}
     */
    show({
      cb
    } = {}) {
      this.messageDisplay = true

      return this.$nextTick(() => {
        this.$refs.pop.show({
          cb: () => {
            setTimeout(() => {
              this.hide()
            }, TIP_DISPLAY_TIME)

            cb && cb()

            return this.$emit('hide')
          }
        })

        return this
      })
    },

    /**
     * pop
     *
     * @param {Object} opt - 
     *                       {Function} cb - 
     * @return {Object}
     */
    hide({
      cb
    } = {}) {
      return this.$refs.pop.hide({
        cb: () => {
          this.messageDisplay = false
          this.isMousedown = false

          this.hideCb && this.hideCb()
          cb && cb()

          return this.$emit('hide')
        }
      })
    },

    /**
     * alert, confirm 
     *
     * @param {String} - 
     * @return {Object, String}
     */
    info(text) {
      if (text === '' || text) {
        this.infoMessage = text
      }

      return this
    },

    /**
     * 
     *
     * @param {Object} opt - 
     *                       {Function} hideCb - 
     *                       {String} type - 
     *                       {Function} message - 
     */
    set({
      hideCb,
      type = 'pop',
      message = ''
    } = {}) {
      this.infoMessage = message
      this.hideCb = hideCb
      this.messageType = type

      return this
    }
  }
}

export default messageComp
