/**
 * slide motion component - 
 *
 * @prop offset - ,
 *                direction  south offsetParent 
 *                direction  north offsetParent 
 *                direction  west offsetParent 
 *                direction  east offsetParent 
 * @prop direction - (north | east | west | south)
 * @prop global -  (fixed) absolute
 * @prop speed - 
 * @prop display - 
 * @prop sync - 
 * @prop once - 
 *
 * @event beforeEnter - 
 * @event enter - 
 * @event afterEnter - 
 * @event beforeLeave - 
 * @event leave - 
 * @event afterLeave - 
 */

import motionMixin from '../../mixin/motion'

export default {
  name: 'MotionSlide',

  mixins: [motionMixin],

  props: {
    direction: {
      type: String,
      default: 'south',
      validator(val) {
        return ['north', 'east', 'west', 'south'].includes(val)
      }
    },
    global: {
      type: Boolean,
      default: false
    },
    offset: {
      type: Number,
      default: 0
    }
  },

  data() {
    this.moving = false // 

    return {
      transiting: false,
      isEnter: false,
      isLeaving: false,
      slideOffset: {}
    }
  },

  computed: {
    translate() {
      return this._getTranslate()
    },
    transition() {
      return `transform ${this.transitionTime} ease-out`
    },
    positionType() {
      return this.global ? 'fixed' : 'absolute'
    }
  },

  methods: {
    /**
     *
     * @param {Object} opt -
     *                      {Number} top
     *                      {Number} left
     * @return {String} - 
     *
     */
    _getTranslate() {
      switch (this.direction) {
        case 'south':
          return `translateY(-100%) translateY(-${this.slideOffset}px)`
        case 'north':
          return `translateY(100%) translateY(${this.slideOffset}px)`
        case 'east':
          return `translateX(-100%) translateY(-${this.slideOffset}px)`
        case 'west':
          return `translateX(100%) translateY(${this.slideOffset}px)`
        default:
          return `translateY(-100%) translateY(-${this.slideOffset}px)`
      }
    },

    /**
     *  offset 
     */
    setOffset(value) {
      this.slideOffset = value

      return this
    },

    beforeEnter({
      code
    } = {}) {
      this.$emit('beforeEnter')

      let el = this.$el

      Object.assign(el.style, {
        'position': this.positionType,
        'transition': this.transition,
        'transform': this._getTranslate()
      })

      return new Promise((resolve, reject) => {
        setTimeout(() => {
          code === this.code && (el.style.display = '')

          return resolve()
        }, 78)
      })
    },

    entering({
      code
    } = {}) {
      let el = this.$el
      // HACK: trigger browser reflow
      let height = el.offsetHeight

      this.$emit('entering')

      Object.assign(el.style, {
        'transform': ''
      })

      return new Promise((resolve, reject) => {
        setTimeout(() => {
          return resolve()
        }, this.time)
      })
    },

    afterEnter() {
      let el = this.$el

      Object.assign(el.style, {
        'position': '',
        'transition': ''
      })

      this.$emit('afterEnter')
    },

    beforeLeave() {
      let el = this.$el

      this.$emit('beforeLeave')

      return Object.assign(el.style, {
        'position': this.positionType,
        'transition': this.transition,
        'transform': ''
      })
    },

    leaveing({
      code
    } = {}) {
      let el = this.$el

      this.$emit('leaving')

      Object.assign(el.style, {
        'transform': this.translate
      })

      return new Promise((resolve, reject) => {
        setTimeout(() => {
          code === this.code && (el.style.display = 'none')

          return resolve()
        }, this.time)
      })
    },

    afterLeave() {
      let el = this.$el

      Object.assign(el.style, {
        'position': '',
        'transition': '',
        'transform': ''
      })

      return this.$emit('afterLeave')
    }
  },

  render(h) {
    return h('transition', this.$slots.default)
  },

  created() {
    this.slideOffset = this.offset
  },

  mounted() {
    if (!this.display) {
      this.$el.style.display = 'none'
    }
  }
}
