/**
 * search 
 *
 * @prop input - 
 * @prop option - 
 * @prop keyword - 
 *
 * @event change - 
 */

import './Search.scss'
import render from './Search.render'
import baseMixin from '../../mixin/base'

import tip from '../Message/tip'
import inputComp from '../Input/Input'
import listComp from '../List/List'

const searchComp = {
  name: 'Search',

  render,

  mixins: [baseMixin],

  components: {
    list: listComp,
    'input-box': inputComp
  },

  props: {
    input: {
      type: Boolean,
      default: true
    },
    option: {
      type: Array,
      default: () => []
    },
    keyword: {
      type: [String, Number],
      default: ''
    }
  },

  data() {
    return {
      // 
      compName: 'search',
      // 
      matchDisplay: false,
      // 
      matchOpt: []
    }
  },

  computed: {
    // 
    cPrefix() {
      return `${this.compPrefix}-search`
    }
  },

  watch: {
    keyword(val) {
      this.search(val)
    }
  },

  methods: {
    _setDataOpt() {
      this.matchOpt = this.option.slice()
    },

    _initComp() {
      this.search(this.keyword)
    },

    /**
     * 
     */
    _clickMatchOpt(event, index) {
      tip('')
    },

    /**
     * 
     * @return {Object} this - 
     */
    search(keyword) {
      if ((keyword === 0 || !keyword) || !(Array.isArray(this.option) && this.option.length > 0)) {
        this.matchOpt = []
      } else {
        this.matchOpt = this.option.filter((item) => {
          return item.text.indexOf(keyword) > -1
        })
      }

      this.$emit('change', {
        emitter: this,
        matchOpt: this.matchOpt
      })

      this.matchDisplay = this.matchOpt.length !== 0
    }
  }
}

export default searchComp
