/**
 * modal 
 *
 * @prop commit -  full 
 *                
 * @prop header - 
 * @prop message - 
 * @prop size - S | M | L
 *
 * @prop okCb -  false 
 * @prop noCb - 
 * @prop okBtn - 
 * @prop noBtn - 
 * @prop noBtnDisplay - 
 *
 * @prop headerDisplay - 
 * @prop footerDisplay - 
 *
 * @prop height -  (Number | 'auto' | '100%')
 * @prop type - full | alert | confirm | simple | long
 *
 * @slot - 
 *
 * @event ok - 
 * @event no - 
 * @event show - 
 * @event hide - 
 */

import '../../scss/common/main.scss'
import './Modal.scss'
import './Modal.m.scss'
import './Modal.material.scss'
import './Modal.bootstrap.scss'

import render from './Modal.render'
import baseMixin from '../../mixin/base'
import apiMixin from './Modal.api'

import Pop from '../Pop/Pop'
import Btn from '../Btn/Btn'
import Icon from '../Icon/Icon'
import Scroller from '../Scroller/Scroller'
import Row from '../Row/Row'
import Col from '../Col/Col'

import MotionFade from '../MotionFade/MotionFade'

import {
  handleEleDisplay
} from '../../util/dom/prop'

const TYPE_ALERT = 'alert'
const TYPE_CONFIRM = 'confirm'
const TYPE_TIP = 'tip'

const TIP_SHOW_TIME = 1500

const modalComp = {
  name: 'Modal',

  render,

  mixins: [baseMixin, apiMixin],

  components: {
    btn: Btn,
    icon: Icon,
    pop: Pop,
    scroller: Scroller,
    row: Row,
    column: Col,
    'motion-fade': MotionFade
  },

  props: {
    commit: {
      type: Boolean,
      default: false
    },
    type: {
      type: String,
      default: 'simple'
    },
    size: {
      type: String,
      default: 'S',
      validator(val) {
        return ['s', 'm', 'l'].includes(val.toLowerCase())
      }
    },
    header: {
      type: String,
      default: ''
    },
    okCb: {
      type: [Function, Boolean],
      default: false
    },
    noCb: {
      type: [Function, Boolean],
      default: false
    },
    okBtn: {
      type: String,
      default: ''
    },
    noBtn: {
      type: String,
      default: ''
    },
    message: {
      type: String,
      default: ''
    },
    headerDisplay: {
      type: Boolean,
      default () {
        return undefined
      }
    },
    headerNoBtnDisplay: {
      type: Boolean,
      default: false
    },
    noBtnDisplay: {
      type: Boolean,
      default: false
    },
    footerDisplay: {
      type: Boolean,
      default () {
        return undefined
      }
    },
    height: [Number, String]
  },

  data: () => {
    return {
      pointStart: {
        x: 0,
        y: 0
      },
      stateUI: '',
      stateTheme: '',
      isMousedown: false,
      modalDisplay: false,
      stateMessage: '',
      stateHeader: '',
      showCb: null, // 
      hideCb: null, // 
      okCbFun: null,
      noCbFun: null,
      hasScroller: false // scroller 
    }
  },

  computed: {
    cPrefix() { // 
      return `${this.compPrefix}-modal`
    },
    uiClass() {
      return `ui-${this.stateUI}`
    },
    themeClass() {
      return `theme-${this.stateTheme}`
    },
    UIMaterial() {
      return this.stateUI === 'material'
    },
    headerClass() { //  header  class 
      return {
        [`${this.cPrefix}-no-header`]: !this.modalHeaderDisplay,
        [`${this.cPrefix}-no-header-title`]: !this.stateHeader
      }
    },
    footerClass() { //  footer  class 
      return {
        [`${this.cPrefix}-no-footer`]: !this.modalFooterDisplay
      }
    },
    isFull() { //  full modal
      return this.type === 'full'
    },
    isSimple() { //  simple modal
      return this.type === 'simple'
    },
    isBiggerFull() { //  full 
      return (this.isFull &&
        (
          this.deviceSize === 'm' ||
          this.deviceSize === 'l' ||
          this.deviceSize === 'xl'
        )
      ) || !this.isFull
    },
    modalHeaderDisplay() { // 
      if (this.headerDisplay !== undefined) {
        return this.headerDisplay
      }

      switch (this.type) {
        case 'full':
          return true
        case 'simple':
          return false
        default:
          return !!this.stateHeader
      }
    },
    modalFooterDisplay() { // 
      if (this.footerDisplay !== undefined) {
        return this.footerDisplay
      }

      switch (this.type) {
        case 'alert':
        case 'confirm':
          return true
        case 'full':
          return this.isBiggerFull
        case 'simple':
          return false
        default:
          return true
      }
    },
    modalHeight() { // 
      if (this.height) {
        return this.height
      }

      switch (this.type) {
        case 'full':
          return this.isBiggerFull ? 300 : '100%'
        case 'simple':
          return 150
        default:
          return 120
      }
    }
  },

  methods: {
    _initComp() {
      this._initModal()
    },

    _binder() {
      this.$refs.pop.$on('showing', () => {
        this.UIMaterial && this.$refs.scroller.initScroller()
        this.$refs.pop.computePosition()
      })

      this.$refs.pop.$on('show', (opt) => {
        this.showCb && this.showCb()

        return this.$emit('show', {
          ...opt,
          emitter: this
        })
      })

      this.$refs.pop.$on('hide', (opt) => {
        this.hideCb && this.hideCb()

        return this.$emit('show', {
          ...opt,
          emitter: this
        })
      })
    },

    _initModal() {
      this.$refs.scroller && this.$refs.scroller.$on('yBarChange', ({
        hasScroller
      }) => {
        this.hasScroller = hasScroller
      })
    },

    /**
     * 
     */
    _setDataOpt() {
      this.stateMessage = this.message
      this.stateHeader = this.header

      this.okCbFun = this.okCb
      this.noCbFun = this.noCb

      this.stateUI = this.ui
      this.stateTheme = this.theme
    },

    /**
     * 
     */
    _handlerClickBg() {
      return this.hide()
    }
  }
}

export default modalComp
