/**
 * row 
 *
 * @prop align -  flex  align-items 
 *    [start, end, center]
 * @prop gap - px-- 
 * @prop justify -  flex  justify-content 
 *    [start, end, center, justify]
 * @prop wrap -  flex  flex-wrap nowrap | wrap
 * @prop type - 
 *
 */

// import './Row.scss'
import render from './Row.render.js'
import baseMixin from '../../mixin/base'

const layoutType = ['grid', 'flex', 'flow']

export default {
  name: 'Row',

  mixins: [baseMixin],

  render,

  props: {
    align: {
      type: String,
      default: 'center'
    },

    gap: {
      type: Number,
      default: 0
    },

    justify: {
      type: String,
      default: 'justify'
    },

    wrap: {
      type: String,
      default: 'wrap'
    },

    type: {
      type: String,
      default: 'flow'
    }
  },

  computed: {
    // 
    cPrefix() {
      return `${this.compPrefix}-row`
    },
    compClass() {
      let compClass = this.xclass([
        `align-${this.align}`,
        `justify-${this.justify}`,
        this.wrap
      ])

      return [compClass, this.cPrefix]
    }
  }
}
