/**
 * menu 
 *
 * @prop store - 
 * @prop coverTrig - TODO pc mobile 
 * @prop noTrig - 
 * @prop height -  auto
 *                1auto
 *                2
 * @prop width -  170
 *               1auto trigger 
 *               2
 * @prop trigHeight -  auto
 *                    1auto
 *                    2
 *
 * @event afterSpread - 
 * @event afterFold - 
 * @event scrollerChange - 
 */

import '../../scss/common/main.scss'
import '../../lib/directive/clickParent'
import './Menu.scss'

import render from './Menu.render'
import store from '../../vuex/store'

import Btn from '../Btn/Btn'
import Icon from '../Icon/Icon'
import Motion from './Motion'
import Scroller from '../Scroller/Scroller'

import baseMixin from '../../mixin/base'
import formMixin from '../../mixin/form'
import apiMixin from './Menu.api'

export default {
  name: 'Menu',

  render,

  mixins: [baseMixin, formMixin, apiMixin],

  store,

  components: {
    btn: Btn,
    motion: Motion,
    icon: Icon,
    scroller: Scroller
  },

  props: {
    store: Object,
    ban: {
      type: Boolean,
      default: false
    },
    noTrig: {
      type: Boolean,
      default: false
    },
    coverTrig: {
      type: Boolean,
      default: false
    },
    height: {
      type: [Number, String],
      default: 'auto',
      validator(val) {
        if (typeof val === 'number') {
          return true
        } else if (val === 'auto') {
          return true
        } else {
          return false
        }
      }
    },
    width: {
      type: [String, Number],
      default: 170,
      validator(val) {
        if (typeof val === 'number') {
          return true
        } else if (val === 'auto') {
          return true
        } else {
          return false
        }
      }
    },
    trigHeight: {
      type: [String, Number],
      default: 'auto',
      validator(val) {
        if (typeof val === 'number') {
          return true
        } else if (val === 'auto') {
          return true
        } else {
          return false
        }
      }
    }
  },

  data() {
    this.compName = 'menu' // 
    this.uid = '' // 

    return {
      focusing: false, //  focus 
      clicking: false, // 
      menuHeight: 0, // 
      menuWidth: 0, // 
      panelDisplay: false, // 
      triggerHeight: 0 // 
    }
  },

  computed: {
    cPrefix() { // 
      return `${this.compPrefix}-menu`
    },
    me() {
      return this
    },
    menuClass() { //  class 
      let classArr = [
        this.cPrefix,
        this.xclass(this.compClass)
      ]

      return classArr
    }
  },

  watch: {
    deviceSize(val) {
      this._changeByDeviceSize(val)
    },
    trigHeight(val) {
      this._adjustTriggerPoiStyle(val)
    }
  },

  methods: {
    _initComp() {
      !this.noTrig && this._adjustTriggerPoiStyle()
    },

    _binder() {
      this.$refs.scroller.$on('change', (opt) => {
        if (this.panelDisplay) {
          this.adjust(opt)
        }

        return this.$emit('scrollerChange', {
          ...opt,
          emitter: this
        })
      })

      if (this.UIMaterial) {
        const $refMotion = this.$refs.motion

        $refMotion.$on('afterEnter', () => {
          this.$emit('afterSpread', {
            emitter: this
          })
        })

        $refMotion.$on('afterLeave', () => {
          this.panelDisplay = false

          this.$emit('afterFold', {
            emitter: this
          })
        })
      }

      if (this.$refs.triggerBtn) {
        this.$refs.triggerBtn.$on('keyEnter', ({
          event
        }) => {
          this.click(event)
        })
      }
    },

    /**
     * 
     */
    _adjustTriggerPoiStyle(trigHeight = this.trigHeight, cb) {
      this.triggerHeight = trigHeight === 'auto' ? this.$refs.trigger.offsetHeight : trigHeight

      return this.$nextTick(() => {
        cb && cb()
      })
    },

    /**
     * 
     */
    _changeByDeviceSize(size) {
      if (this.panelDisplay) {
        this.spread()
      }
    },

    /**
     * 
     *
     * @param {Boolean} optVal - ,
     *                        false:  true: undefined 
     *
     * @return {Object} - this
     */
    _togglePanelDisplay(optVal = !this.panelDisplay) {
      const getMenuHeight = (vm) => {
        const $panel = vm.$refs.panel

        $panel.style.visibility = 'hidden'
        $panel.style.display = ''

        if (vm.height === 'auto') {
          let scrollerComp = vm.$refs.scroller
          scrollerComp._initScroller()

          vm.menuHeight = scrollerComp.scrollerHeight
        } else {
          vm.menuHeight = vm.height
        }
      }

      const transite = (state, vm) => {
        if (state) {
          getMenuHeight(vm)

          vm.panelDisplay = true

          if (this.UIMaterial) {
            vm.$refs.motion.enter()
          } else {
            this.$nextTick(() => {
              this.$emit('afterSpread', {
                emitter: this
              })
            })
          }
        } else {
          vm.panelDisplay = false
          if (this.UIMaterial) {
            vm.$refs.motion.leave()
          } else {
            this.$nextTick(() => {
              this.$emit('afterFold', {
                emitter: this
              })
            })
          }
        }
      }

      if (this.noTrig) {
        return transite(optVal, this)
      } else {
        return this._adjustTriggerPoiStyle(transite(optVal, this))
      }
    }
  }
}
