/**
 * input 
 *
 * @prop block - 
 * @prop hidden - 
 * @prop value - 
 * @prop label - 
 * @prop multiline - 
 * @prop number -  nmuber 
 * @prop placeholder - 
 * @prop param - 
 * @prop readOnly - 
 * @prop required - 
 * @prop row - textarea 
 * @prop textLengthTip - 
 * @prop type - ( field | area )
 *
 * @prop completion - 
 * @prop helperText - 
 * @prop errorText -  regex 
 * @prop errorTipType -  bottom | tip 
 * @prop min - inputtextarea 
 * @prop max - inputtextarea 
 * @prop minNum - inputtextarea 
 * @prop maxNum - inputtextarea 
 * @prop regex - 
 * @prop activeVerify - 
 * @prop verifiedType -  name 
 *
 * @prop headerSpan - 
 * @prop footerSpan - 
 *
 * @event change - input
 * @event blur - inputblur
 * @event focus - inputfocus
 * @event keyup - inputkeyup
 *
 * @slot header
 * @slot footer
 */

import '../../lib/directive/focus'
import '../../scss/common/main.scss'
import './Input.scss'
import './Input.material.scss'
import './Input.bootstrap.scss'
import render from './Input.render'

import store from '../../vuex/store'
import hubStore from '../../vuex/module/hub/type.json'
import initVerfication from './validate.js'

import baseMixin from '../../mixin/base'
import formMixin from '../../mixin/form'
import apiMixin from './Input.api'

import Row from '../Row/Row'
import Col from '../Col/Col'
import MotionFade from '../MotionFade/MotionFade'

const TYPE_TEXT_AREA = 'area'
const TYPE_TEXT = 'field'
const ERROR_MESSAGE_TIP = 'tip'
const ERROR_MESSAGE_BUBBLE = 'bubble'
const KEYUP_INTERVAL_TIME = 100

export default {
  name: 'Input',

  render,

  mixins: [baseMixin, formMixin, apiMixin],

  components: {
    row: Row,
    column: Col,
    'motion-fade': MotionFade
  },

  store,

  props: {
    activeVerify: {
      type: Boolean,
      default: false
    },
    block: {
      type: Boolean,
      default: false
    },
    hidden: {
      type: Boolean,
      default: false
    },
    helperText: {
      type: String,
      default: ''
    },
    value: {
      type: [String, Number],
      default: ''
    },
    label: {
      type: String,
      default: ''
    },
    multiline: {
      type: Boolean,
      default: false
    },
    number: {
      type: Boolean,
      default: false
    },
    placeholder: {
      type: String,
      default: ''
    },
    param: {
      type: String,
      default: ''
    },
    readOnly: {
      type: Boolean,
      default: false
    },
    row: {
      type: Number,
      default: 4
    },
    textLengthTip: {
      type: Boolean,
      default: false
    },
    type: {
      type: String,
      default: 'field'
    },
    required: {
      type: Boolean,
      default: false
    },
    errorText: {
      type: String,
      default: ''
    },
    errorTipType: {
      type: String,
      default: 'bottom',
      validator(val) {
        return ['bottom', 'pop'].includes(val)
      }
    },
    min: Number,
    max: Number,
    minNum: Number,
    maxNum: Number,
    regex: String,
    verifiedType: String,
    completion: {
      type: Boolean,
      default: false
    },
    headerSpan: {
      type: Number,
      default: 1
    },
    footerSpan: {
      type: Number,
      default: 1
    }
  },

  data() {
    this.compName = 'input' // 

    return {
      stateValue: this.number ? this._switchNum(this.value) : this.value, // 
      focusing: false, //  focus 
      keyuping: false, //  keyup 
      verifiedHint: '', // 
      dataTypeName: '', // 
      verified: true, // 
      inputTextLength: 0, // 
      editBorderActive: false //  material 
    }
  },

  computed: {
    cPrefix() { // 
      return `${this.compPrefix}-input`
    },
    formatMessage() { // 
      if (this.regex) {
        return this.errorMsg
      } else if (this.verifiedType) {
        return `${this.dataTypeName}`
      } else {
        return `${this.name}`
      }
    },
    placeholderDisplay() { // 
      const empty = this.stateValue === '' || this.stateValue === undefined

      if (this.UIMaterial) {
        if (this.label) {
          return this.focusing && empty
        } else {
          return empty
        }
      } else {
        return empty
      }
    },
    labelDisplay() { // 
      return this.UIMaterial && !!this.label
    },
    labelFloatDisplay() { // 
      if (this.focusing) {
        return true
      } else {
        return !(this.stateValue === '' || this.stateValue === undefined)
      }
    },
    errorTextDisplay() { // 
      return !!this.verifiedHint && this.errorTipType === 'bottom'
    },
    errorBorderDisplay() { // 
      return this.ui === 'bootstrap' && !!this.verifiedHint
    },
    isTextarea() {
      return this.type === TYPE_TEXT_AREA
    },
    isText() {
      return this.type === TYPE_TEXT
    },
    inputHub() {
      return this.$store.getters[hubStore.input.get]
    },
    stageClass() { //  stage  class 
      return [
        this.cPrefix,
        this.xclass(`type-${this.type}`),
        this.xclass([this.themeClass, this.uiClass]),
        {
          [`${this.cPrefix}-textarea-stage`]: this.isTextarea,
          [this.xclass('label-cover')]: this.labelDisplay && !this.labelFloatDisplay,
          [this.xclass('block')]: this.block
        }
      ]
    },
    wrapClass() {
      return [
        this.xclass('wrap'),
        {
          [`${this.cPrefix}-editting`]: this.focusing
        },
        {
          [`${this.cPrefix}-error`]: this.errorTextDisplay
        },
        {
          [`${this.cPrefix}-error-border`]: this.errorBorderDisplay
        }
      ]
    },
    inputBoxCol() { // input 
      let slotHead = this.$slots.header
      let slotTail = this.$slots.footer

      if (slotHead && slotTail) {
        return 12 - this.footerSpan - this.headerSpan
      } else if (slotHead) {
        return 12 - this.headerSpan
      } else if (slotTail) {
        return 12 - this.footerSpan
      } else {
        return 12
      }
    }
  },

  watch: {
    value(val, oldVal) {
      this.stateValue = val
    },
    stateValue(val, oldVal) {
      const valueLength = String(val).length

      if (this.textLengthTip) {
        if (valueLength <= this.max) {
          this._dispatchChange()
          this.inputTextLength = valueLength
        } else {
          this.stateValue = val.substr(0, this.max)
        }
      } else {
        this._dispatchChange()
      }

      if (this.completion && this.$slots.completion) {
        this.$slots.completion[0].componentInstance.search(val)
      }
    },
    errorTextDisplay(val) {
      if (this.tipDisplay) {
        val ? this.$refs.helperTip.leave() : this.$refs.verifiedHint.leave()
      }
    },
    placeholderDisplay(val) {
      const refPalceholder = this.$refs.palceholder

      if (!refPalceholder) {
        return false
      }

      val ? refPalceholder.enter() : refPalceholder.leave()
    }
  },

  methods: {
    _binder() {
      if (this.tipDisplay) {
        this.$refs.verifiedHint.$on('afterLeave', () => {
          this.$refs.helperTip.enter()
        })

        this.$refs.helperTip.$on('afterLeave', () => {
          this.$refs.verifiedHint.enter()
        })
      }
    },

    /**
     * 
     * @return {Object} this - 
     */
    _initVerfication() {
      if (this.regex) {
        this.regexObj = new RegExp(this.regex)

        return this
      }

      var verify = initVerfication(this.verifiedType)

      if (verify) {
        this.regexObj = verify.regex
        this.dataTypeName = verify.dataTypeName
      }

      return this
    },

    /**
     *  value  change 
     * @return {Object} this - 
     */
    _dispatchChange() {
      return this.$emit('change', {
        emitter: this,
        value: this.stateValue
      })
    },

    /**
     * 
     *
     * @return {Object} -
     *                  verified - 
     *                  verifiedHint - 
     */
    _verifyEmpty(firstVerify) {
      let verifiedHint = ''

      if (this.required) {
        verifiedHint = `${this.name}`

        return {
          verified: false,
          verifiedHint
        }
      }

      return {
        verified: true,
        verifiedHint
      }
    },

    /**
     *  -  16 
     */
    _switchNum(val) {
      if (val === 0 || val === '0') {
        return 0
      }

      let strTemp = String(val)

      if (isNaN(strTemp)) {
        let temp = strTemp

        strTemp = strTemp.replace(/[^\d.]+/g, '')

        if (temp.indexOf('-') === 0) {
          strTemp = '-' + strTemp
        }
      }

      if (isNaN(strTemp)) {
        strTemp = ''
      }

      if (val.length >= 16) {
        return strTemp
      }

      return Number(strTemp)
    },

    /**
     *  focus 
     * @return {Object} this - 
     */
    _handlerFocus(evt) {
      this.editBorderActive = true
      this.verified = true
      this.focusing = true

      if (this.activeVerify) {
        this.editBorderActive = true
      } else {
        this.editBorderActive = !this.errorTextDisplay
      }

      return this.$emit('focus', {
        emitter: this,
        valeu: this.stateValue,
        event: evt
      })
    },

    /**
     *  blur 
     * @return {Object} this - 
     */
    _handlerBlur(evt) {
      this.focusing = false

      if (this.number) {
        this.stateValue = this._switchNum(this.stateValue)
      }

      if (this.activeVerify) {
        this.editBorderActive = false
      } else {
        this.verify()

        this.editBorderActive = this.errorTextDisplay
      }

      return this.$emit('blur', {
        emitter: this,
        valeu: this.stateValue,
        event: evt
      })
    },

    /**
     *  keyup 
     * @return {Object}
     */
    _handlerKeyup() {
      if (this.keyuping) {
        return false
      }

      this.keyuping = true

      setTimeout(() => {
        this.keyuping = false
      }, KEYUP_INTERVAL_TIME)
    },

    /**
     * input 
     * @return {Object}
     */
    _handlerInput(event) {
      const refInput = this.$refs.input

      this.stateValue = event.currentTarget.value
      this.multiline && (this.$refs.pre.innerText = this.stateValue)

      if (this.focusing && this.errorTextDisplay) {
        this.verify()
        this.editBorderActive = !this.errorTextDisplay
      }
    }
  },

  created() {
    this.placeholderStartedDisplay = this.placeholderDisplay // 
    // 
    this.tipDisplay = this.helperText || this.required || this.verifiedType || this.regex ||
      this.max || this.min || this.maxNum || this.minNum
  },

  mounted() {
    this._initVerfication()

    this.$store.dispatch(hubStore.input.add, this)
  }
}
