/**
 * icon 
 *
 * @prop theme - 
 * @prop size - (xs, s, m, l, xl),  s
 * @prop color -  16 
 * @prop type -  ( class )
 * @prop kind - exfa-circle -> kind='circle'ali-fold -> kind='fold')
 *
 */

import '../../asset/icon/iconfont.svg.js' // iconfont  svg 
import './Icon.scss'

import Vue from 'vue'
import render from './Icon.render.js'
import baseMixin from '../../mixin/base'

const TYPE_ALI = 'ali'
const TYPE_FA = 'fa'

export default {
  name: 'Icon',

  render,

  mixins: [baseMixin],

  props: {
    color: {
      type: String,
      default: ''
    },

    size: {
      type: String,
      default: 's',
      validator(val) {
        return ['xs', 's', 'm', 'l', 'xl'].includes(val.toLowerCase())
      }
    },

    type: {
      type: String,
      default: TYPE_ALI
    },

    kind: {
      type: String,
      require: true
    }
  },

  computed: {
    cPrefix() { // 
      return `${this.compPrefix}-icon`
    },
    sizeClass() {
      return `${this.compPrefix}-icon-size-${this.size.toLowerCase()}`
    },
    isAli() {
      return this.type === 'ali'
    },
    typeClass() {
      return this.isAli ? `${this.compPrefix}-icon-${this.type}` : this.type
    },
    nameClass() {
      return this.isAli ? `ali-icon-${this.kind}` : `fa-${this.kind}`
    }
  }
}
