/**
 * shift - 
 *
 * @prop index - 
 * @prop type -  \
 * @prop before -  class 
 * @prop after -  class 
 *
 */

import './Shift.scss'
import render from './Shift.render.js'
import baseMixin from '../../mixin/base'
import Row from '../Row/Row'
import Col from '../Col/Col'

// 
const SHIFT_TYPE = ['display', 'move', 'opacity']

export default {
  name: 'Shift',

  render,

  mixins: [baseMixin],

  components: {
    row: Row,
    column: Col
  },

  props: {
    after: String,
    before: String,
    index: {
      type: Number,
      default: 1
    },
    justify: {
      type: String,
      default: 'justify'
    },
    type: {
      type: String,
      default: 'display',
      validator(val) {
        return SHIFT_TYPE.includes(val)
      }
    }
  },

  data() {
    return {
      //  shift 
      currentIndex: 0,
      //  $slot  key 
      shiftSlotKey: 0,
      //  $slot  key 
      shiftNum: 0
    }
  },

  computed: {
    // 
    cPrefix() {
      return `${this.compPrefix}-shift`
    },
    //  class
    beforeClass() {
      if (this.before) {
        return this.before
      } else if (this.after) {
        return ''
      } else {
        return `${this.cPrefix}-before-${this.type}`
      }
    },
    //  class
    afterClass() {
      if (this.after) {
        return this.after
      } else if (this.before) {
        return ''
      } else {
        return `${this.cPrefix}-after-${this.type}`
      }
    }
  },

  watch: {
    index(val) {
      this.currentIndex = val
    }
  },

  methods: {
    _setDataOpt() {
      this.currentIndex = this.index

      this.$slotKey.forEach((item, index) => {
        if (item !== 'default') {
          this.shiftNum++
        }
      })
    },

    /**
     *  index
     *
     * @return {Object}
     */
    switch(index) {
      this.currentIndex = index
    },

    /**
     * 
     *
     * @return {Object}
     */
    next() {
      this.currentIndex + 1 <= this.shiftNum && this.currentIndex++

      return this
    },

    /**
     * 
     *
     * @return {Object}
     */
    pre() {
      this.currentIndex - 1 > 0 && this.currentIndex--

      return this
    },

    /**
     * 
     *
     * @return {Object}
     */
    rotate() {
      if (this.currentIndex + 1 > this.shiftNum) {
        this.currentIndex = 1
      } else {
        this.currentIndex++
      }

      return this
    }
  }
}
